import navtop from "../../components/nav_top/nav_top.vue"
	export default {
		data() {
			return {
				navtop: {
							title: '四川弘浩物流',
							color: 'black'
						},
						infos:{},
						number:'',
						numbers:''
			}
		},
		components: {
			navtop
		},
		onLoad:function(option){//opthin为object类型，会序列化上页面传递的参数
		this.number=option.number
		this.requst(this.number)
			console.log(option.number);//打印出上页面传递的参数
		},
		methods: {
			tomore(){
				uni.navigateTo({
					url:`../ordermains/ordermain?number=`+this.number
				})
			},
			requst(number){
				uni.request({
					url: 'http://hhwl.public.5151fw.com/wxapp/login/getHh',
					method: 'POST',
					data: {number:number,type:1},
					success: res => {
						if(res.data.data.result==1){
							uni.showToast({
								title:res.data.data.msg,
								icon: "none"
							})
						}else if(res.data.data.result==0){
							console.log(res.data.data);
							this.infos=res.data.data.msg
							console.log(this.infos);
							
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
	tosearchlist(){
		console.log(1);
		if(this.numbers==''){
			uni.showToast({
			    title: "订单号/货号为空",
			    icon: "none"
			})
		}else{
			uni.request({
				url: 'http://hhwl.public.5151fw.com/wxapp/login/getHh',
				method: 'POST',
				 header: {'content-type': 'application/x-www-form-urlencoded'},
				data: {number:this.numbers,type:1},
				success: res => {
					if(res.data.data.result==1){
						uni.showToast({
							title:res.data.data.msg,
							icon: "none"
						})
					}else if(res.data.data.result==0){
						uni.showLoading({
							title:'跳转中....'
						})
						setTimeout(function() {
						  uni.hideLoading()
						}, 10);
						uni.navigateTo({
							url:`../searchlist/searchlist?number=`+this.numbers
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
		
		
	},
			
		}
	}
